<template>
	<view class="conetentinfo-wrap">
		<!-- 内容列表 -->
		<view class="course-wrap">
			<view @click="viewFeaturedInfo(item.id)" class="live-list" v-for="(item, index) in list_info" :key="index">
				<view class="live-list-img-wrap">
					<image class="live-list-img" :src="item.thumb" mode="aspectFill"></image>
					<text class="course-title-icon">套餐</text>
				</view>

				<view class="live-list-info">
					<view class="live-c-title">{{item.name}}</view>
					<view class="live-status">
						{{item.nums +'课时'}}
					</view>
					<view class="live-teacher-price">
						<view class="live-teacher-price" v-for="(item,index) in item.teacher" :key="index">
							<image class="live-teacher-avatar" :src="item.avatar" mode="aspectFill"></image>
						</view>
						<view class="price-wrap">
							<text class="numPrice">
								{{'¥' + item.price}}
							</text>
						</view>
					</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	const app = getApp();

	export default {
		data() {
			return {
				list_info: {}
			}
		},
		onLoad() {
			this.getContentCourseList();
		},
		methods: {
			getContentCourseList() {
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Package.GetList',
					method: 'GET',
					data: {
						'gradeid': gData.grade_class.id,
						'p': 1
					},
					success: res => {
						if (parseInt(res.data.data.code) !== 0) {
							return;
						}

						if (res.data.data.info.length < 1) {
							// 空空如也
						}
						console.log(res);
						this.list_info = res.data.data.info;

					},
				});
			},

			viewFeaturedInfo(contentCourseId) {
				console.log(contentCourseId);
				uni.navigateTo({
					url: '../../packageB/pages/tancaninfo/taocaninfo?courseid=' + contentCourseId,

				});
			},

		}
	}
</script>

<style>
	/* 大班课内容列表公共样式 */
	@import url("/static/css/course_list.css");

	page{
		background-color: #F5F5F5;
	}
	.conetentinfo-wrap {
		margin-top: 2rpx;
		padding-top: 2rpx;
		min-height: 1500rpx;
		background-color: #FFFFFF;
	}


	.live-list {
		width: 90%;
		height: 190rpx;
		margin: 30rpx auto;
		padding-top: 5rpx;
		border-radius: 8rpx;
		background-color: #FFFFFF;
	}

	.live-c-title {
		line-height: 35rpx;
		height: 40%;
	}

	.live-teacher-price {
		margin-top: 5rpx;
	}

	.price-wrap {
		margin-left: 55% !important;
	}
</style>
